.title-over {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background: #000;
  padding: 30px 0;
  .item-over {
    align-self: center;
    .sm-block({
     
        text-align: center;
    });
    ul {
    
      display: flex;
      .sm-block({
          flex-wrap: wrap;
          flex-direction: column;
          padding: 0;
      });
      .md-block({
        flex-wrap: wrap;
        justify-content: center;
      });
      li {
          margin: 0 10px;
        list-style: none;
        
        a {
          font-size: 20px;
          font-family: 'Bebas Neue', sans-serif;
          color: rgb(255, 255, 255);
          img{
              .sm-block({
                  text-align: center;
              });
          }
        }
      }
    }
  }
}