.services {
  background-color: #191919;
  padding: 60px;
  width: 100%;
  h2 {
    font-family: 'Bebas Neue', sans-serif;
    text-align: center;
    font-size: 40px;
    color: #c9c9c9;
    margin-bottom: 50px;
    strong {
      color: #fff;
    }
    span {
      color: #ca2e22;
    }
    img {
      margin: auto 15px auto 15px;
    }
  }
  .nav-tabs {
    display: flex;
    flex-wrap: wrap;
    text-transform: uppercase;
    justify-content: center;
    .nav-link {
      justify-content: center;
      text-align: center;
      border: 1px solid #fff;
      width: 160px;
      margin: 0 15px 20px 15px;
      display: flex;
      flex-direction: column;
      padding: 0;
      padding-top: 20px;
      &:nth-child(1) {
        margin-left: 0;
        .xl-block( {
          margin-left: 15px;
        }
        );
      }
      ;
      &:nth-child(6) {
        margin-right: 0;
        .xl-block( {
          margin-right: 15px;
        }
        );
      }
      ;
      &.active {
        background-color: #2a2a2a;
        button {
          color: #fff;
          background: #353535;
          border: none;
        }
      }
      img {
        margin: 20px auto;
        margin-top: auto;
      }
      p {
        font-size: 14px;
        color: #fff;
        font-family: 'Bebas Neue', sans-serif;
        text-align: center;
        margin-top: auto;
      }
      button {
        background: #212121;
        border: none;
        color: #732821;
      }
    }
  }
  .tab-content {
    .tab-pane {
      text-align: center;
      &.active {
        background: #353535;
        padding: 10px 10px;
        .sm-block( {
          display: flex;
          flex-direction: column;
        }
        );
        a {
          color: #fff;
          font-size: 20px;
          margin: 0 8px;
          .sm-block( {
            font-size: 17px;
          }
          );
        }
      }
      font-family: 'Bebas Neue',
      sans-serif;
      text-decoration: underline; // height: 51px;
     
    }
  }
  .forms{
    text-align: center;
    padding-top: 20px;
   
   .btn-lg{
     font-size: 18px;
     background-color: #d12f22;
   }
  } 
}