.menu {
  background: #121212;
  font-family: 'Bebas Neue', sans-serif;
  .navbar {
    padding: 0;
    .navbar-toggler{
      background: #5d5b5e;
      border-color: #fff;
    }
    .navbar-collapse {
      justify-content: center;
      .nav-item {
        border-left: 1px solid rgb(123, 123, 123);
        padding: 12px 0;
        // &:nth-last-child(){
        //   border-right: 1px solid rgb(123, 123, 123);
        // }
        &:hover{
          background-color: #000;
        
        }
        .nav-link{
          padding: 8px 20px;
         
        }
        span {
          font-size: 18px;
          color: #fff;
          margin: 0 10px;
        }
      }
    }
    form {
      margin-left: 40px; 
      a {
        display: flex;
        border: 3px solid #fff;
        text-align: center;
        h4 {
          font-size: 24px;
          color: #fff;
         margin-bottom: 0;
        }
        span {
          align-self: center;
          font-size: 40px;
        }
        p {
          align-self: center;
          font-size: 18px;
          font-family: 'Myriad Pro', sans-serif;
          text-transform: uppercase;
          color: #fff;
          line-height: 16px;
          margin: 0 10px;
        }
      }
    }
  }
}