.about-us {
  .title {
    background-color: #e9e9e9;
    justify-content: center; h4 {
    font-size: 40px;
    color: rgb(5, 5, 5);
    text-align: center;
    padding-top: 30px;
    &::before {
      content: "\f038";
      font-family: 'FontAwesome', sans-serif;
      font-size: 30px;
    }
    &::after {
      content: "\f036";
      font-family: 'FontAwesome', sans-serif;
      font-size: 30px;
    }
    .sm-block( {
      font-size: 32px;
      line-height: 40px;
    }
    );
  }
  p {
    padding-bottom: 30px;
    font-size: 16px;
    font-family: 'Myriad Pro', sans-serif;
    margin-bottom: 0;
    color: rgb(5, 5, 5);
    text-align: center;
    .sm-block( {
      font-size: 12px;
    }
    );
  }
  }
 
  .about-top {
    background-color: #d6d6d6;
    .content {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      .about-item {
        padding-top: 3%;
        margin: 15px;
        &::before{
          content:"";
          background: #ac160a;
          display: block;
          width: 100%;
          height: 10px;
        }
       
        max-width: 320px;
        img {
          margin-bottom: 25px;
         
        }
        p {
          margin: 0;
          font-size: 14px;
          color: rgb(5, 5, 5);
          line-height: 1.429;
        }
      }
    }
  }
}