header {
  background-image: url('../img/headerbg.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 100vh;
  position: relative;
  .header-top {
    border-style: solid;
    border-width: 1px;
    border-color: #5b5b5b;
    background-color: rgba(0, 0, 0, 0.859);
    padding: 20px 0;
    .item-top {
      position: relative;
      display: flex;
      .sm-block( {
        justify-content: center;
      }
      );
      
      img {
        margin: auto 18px auto 0;
      }
      p {
        font-size: 32px;
        font-family: 'Bebas Neue', sans-serif;
        color: rgb(209, 47, 34);
        margin-bottom: 0;
        line-height: 30px;
        .lg-block( {
          font-size: 28px;
        }
        );
        .md-block( {
          font-size: 20px;
        }
        );
        span {
          color: #fff;
        }
        a {
          font-size: 22px;
          color: #fff;
          .lg-block( {
            font-size: 18px;
          }
          );
          .md-block( {
            font-size: 12px;
          }
          );
        }
      }
      &:nth-child(3) {
        justify-content: end;
        .sm-block( {
          justify-content: center;
        }
        );
      }
      >.logo {
        position: absolute;
        top: -10px;
        width: 100%;
        text-align: center;
        >img {
          margin: 0 auto;
        }
      }
    }
  }
  .header-title {
    margin-top: 74px;
    font-size: 40px;
    font-family: 'Bebas Neue', sans-serif;
    color: rgb(255, 255, 255);
    text-align: center;
    text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.55);
    .sm-block({
      font-size: 40px;
    });
  }
  .header-btn {
    display: flex;
    position: absolute;
    width: 100%;
    bottom: 20px;
    .btn-item {
      display: flex;
      .sm-block( {
        justify-content: center;
        flex-direction: column;
      }
      );
      .btn-top {
        display: flex;
        border: 1px solid #d12f22;
        background-color: #d12f22; // width: 240px;
        // height: 50px;
        align-self: center;
        padding: 6px 11px;
        margin-left: 10%;
        .sm-block( {
          margin-left: 0%;
        }
        );
        img {
          margin: auto 20px;
        }
        p {
          line-height: 18px;
          font-size: 20px;
          font-family: 'Bebas Neue', sans-serif;
          color: rgb(255, 255, 255);
          margin: 0;
          .sm-block( {
            font-size: 14px;
          }
          );
        }
      }
      .btn-top1 {
        border: 1px solid #d12f22;
        background-color: #d12f22; // height: 50px;
        // width: 96px;
        padding: 9px 24px;
        margin-left: 2%;
        font-size: 20px;
        font-family: 'Bebas Neue', sans-serif;
        color: rgb(255, 255, 255);
        align-self: center;
        margin: 6px;
        .sm-block( {
          font-size: 14px;
        }
        );
      }
    }
  }
}
